.posterMovieWrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%; }


.posterImg {
  object-fit: cover;
  height: 380px;
  width: 100%;
  border-radius: 5px;
  margin: 0 10px 10px 10px; }

.posterMovieTitle {
  display: flex;
  text-align: center;
  align-items: flex-start;
  flex-grow: 1;
  font-weight: bold;
  padding-top: 10px;
  font-size: 16px;
  text-transform: uppercase; }


.buttonBlock {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 50%;
  height: 10%; }

